.Theme-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 75%;
  height: 210px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.06);
  background-color: white;

  border-radius: 10px;
  margin-top: 22px;

  padding: 15px;
  z-index: 10;

  > h1 {
    padding: 0;
    font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
    font-weight: 300;
    font-style: normal;
    margin: 0 0 1px;
    color: #576574;
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
}

.Themes {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-self: flex-start;
  flex-wrap: wrap;

  .Theme {
    height: 70px;
    flex: 1 1 auto;
    border: none;
    outline: none;
    width: 80px;
    margin: 10px;
    border-radius: 6px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.06);

    &:hover {
      cursor: pointer;
      border: 2px solid gold;
    }
  }
}

.popup-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  // padding-top: 22px;

  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.55);

  z-index: 10;
}

.Fade-In {
  animation: Fading 0.2s ease-in;
}

@keyframes Fading {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Pallete-Close-Button {
  position: absolute;
  background: none;
  border: none;
  outline: none;
  width: 32px;
  height: 32px;
  right: 60px;
  top: 25px;
  z-index: 20;

  > img {
    width: 100%;
    height: 100%;
  }

  &:hover {
    cursor: pointer;
  }
}
