.Root {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  width: 100%;
  max-height: inherit;
}

.ReCAPTCHA-Container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /* Stretches to fit the height. */
    flex: 1 1 auto;
    height: auto;

    overflow: auto;
    background-color: rgb(249, 249, 249);
}

.ReCAPTCHA-Wrapper-Container {
    
    display: flex;
    flex-direction: column;
    width: 100%;
    
}

.Results-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  max-height: inherit;
  flex: 1 1 auto;
  margin: 10px;
}

.Result-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  /* Stretches to fit the height. */
  flex: 1 1 auto;
  width: 100%;
  max-height: 450px;

  overflow: auto;
  background-color: rgb(249, 249, 249);

  .Result-Wrapper-Container {
    display: flex;
    flex-direction: column;
    width: 100%;

    .Result {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;

      background-color: white;
      border-radius: 5px;

      padding: 10px;
      margin-top: 15px;

      flex: 1 1 auto;

      .Result-Title {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        width: 100%;

        .Result-Header {
          font-size: 0.8rem;
          font-weight: 500;
          padding: 0;
          margin: 0px;
        }

        .Result-Visit-Website-Button {
          background: none;
          outline: none;
          border: none;

          margin: 0;
          padding: 0;

          font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial,
            sans-serif;

          font-size: 0.8rem;
          font-weight: 500;

          &:hover {
            cursor: pointer;
          }
        }
      }

      .Result-Answer {
        font-size: 0.8rem;
        margin-top: 15px;
        margin-bottom: 15px;
        white-space: pre-wrap;
        color: #576574;
      }

      .Span {
        flex: 1 1 auto;
      }

      .Result-Confidence {
        font-size: 0.8rem;
        align-self: flex-end;
        color: #576574;
      }
    }
  }
}

.Results-Settings-Container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  width: 100%;

  .Results-Label {
    font-size: 0.9rem;
    color: #576574;
  }

  .Span-Stretch {
    flex: 1 1 auto;
  }

  .Button-Container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

    .Button-Wrapper {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;

      margin-left: 10px;

      .Button-Label {
        font-size: 0.9rem;
        color: #576574;
      }

      .Button {
        background: transparent;
        border: none;
        outline: none;

        width: 28px;
        margin-left: 5px;

        .Button-Image {
          width: 100%;
        }

        &:hover {
          cursor: pointer;
        }
      }
    }
  }
}

.Loading-Container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  flex: 1 1 auto;
  margin-top: 60px;
}


.No-Results {
  display: flex;
  flex-direction: column;
  font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
  justify-content: center;
  align-items: center;
  flex: 1 1 auto;
  width: 100%;
  height: 150px;

  > p {
    margin-bottom: 10px;
  }
}

.Captcha {
  display: flex;
  flex-direction: column;
  font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
  justify-content: center;
  align-items: center;
  flex: 1 1 auto;
  width: 100%;
  position: absolute;
  bottom: 30px;

  > p {
    margin-bottom: 10px;
  }
}

