.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  width: 100%;
  height: auto;
  max-height: inherit;

  background-color: rgb(248, 249, 250);
}

.App-header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;

  width: 100%;

  background-color: #54a0fc;

  .App-Header-Wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    flex: 1 1 auto;
    margin: 15px 10px 15px 10px;

    > h1 {
      color: white;
      font-size: 1.2rem;
      font-weight: 450;

      margin: 0;
      padding: 0;
    }

    .search-bar {
      margin: 0;
      padding: 0;
      margin-left: 15px;
      background-color: #2e86de;
      border-radius: 5px;
      padding: 8px;
      padding-left: 10px;
      border: none;
      color: white;
      flex: 1 1 auto;
    }

    ::placeholder {
      font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial,
        sans-serif;
      color: white;
      opacity: 0.5;
    }

    > button {
      background: none;
      border: none;

      width: 38px;
      margin-left: 10px;

      > img {
        width: 100%;
      }
    }

    > button:hover {
      cursor: pointer;
    }
  }
}

.App-link {
  color: #61dafb;
}

.Quizzard-Gold::after {
  content: "Gold";
  display: inline-flex;
  color: #ffda6c /* #feca57 */;
  font-size: 0.7rem;
  font-weight: 700;
  transform: translate(0px, 12px) rotate(-25deg);
}
