.Auth-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  margin-top: 2vh;

  width: 75%;
  // max-height: 265px;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.06);
  background-color: #fff;
  border-radius: 5px;
  padding: 15px;
  z-index: 10;

  >h1 {
    padding: 0;
    font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #2e86de;
    font-size: 1.2rem;
    margin: 0 0 1rem;
  }
}

.Auth-Loading-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  margin-top: 40px;
  
  >h1 {
    font-size: 1.25rem;
    font-weight: 500;
    position: relative;
    top: 30%;
    transform: translateY(-50%);
  }
}

.Header-Input-Container {

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;

  width: 100%;
  flex: 1 1 auto;
  // margin-bottom: 14vh;

  .Error-Code-Label {
    font-weight: 500;
    opacity: 0.3;
  }

  .Flex-Region {
    flex: 1 1 auto;
  }

  >button {
    background: none;
    border: none;
    outline: none;

    >img {
      width: 15px;

      &:hover {
        cursor: pointer;
      }
    }
  }
}

.Auth-Alternative-Sign-In-Container {

  display: flex;
  flex-direction: row;
  gap: 2vw;
  margin-top: 11px;
  // margin-bottom: 5px;
  position: relative;
  right: 4px;

  .Auth-Alternative-Sign-In-Button {

    background: transparent;
    border: none;
    outline: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1vw;
    border: 1px solid #ccc;
    padding-top: 4px;
    padding-bottom: 4px;
    font-size: 0.75rem;
  
    > img {
      height: 13px;
    }
  
    &:hover {
      cursor: pointer;
      background-color: #eee;
    }

  }

}


.Auth-Logo-Container {

  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  flex: 1 1 auto;
  text-align: center;

  >h1 {
    color: black;
    font-weight: 500;
    font-size: 1.5rem;
  }
}

.Auth-Activate-Message {
  font-size: 0.75rem;
  font-weight: 500;
  color: #576574;
  width: 85%;
  text-align: center;
  padding-bottom: 1.25rem;
}

.Forgot-Password-Success-Icon {
  margin-top: 10px;
  margin-bottom: 10px;
}

.Auth-Forgot-Password-Message {
  font-size: .75rem;
  font-weight: 500;
  color: #576574;
  width: 85%;
  text-align: center;
  padding: 5px 0 5px 0px;
  margin: 5px 0 5px 0px;
}

.Auth-Button-Container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  width: 100%;
  flex: 1 1 auto;
}

.Auth-Button {
  background: none;
  border: none;
  outline: none;
  border-radius: 5px;
  text-align: center;
  background-color: #19AC84;
  color: #fff;
  width: 50%;
  align-self: center;
  height: 150px;
  margin-top: 10px;

  &:hover {
    cursor: pointer;
  }
}

.Auth-Wrapper-Container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  flex: 1 1 auto;
  padding: 15px;
}

.Auth-Error-Container {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 2vh;
  margin-top: 7vh;
  margin-bottom: 5vh;

  >h1 {
    color: black;
    font-weight: 500;
    font-size: 1.35rem;
  }

  > div {

    margin-top: 10px;

    > img {
      width: 75px;
    } 
  }


}

.Auth-Error-Button-Container {

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  width: 75%;
  flex: 1 1 auto;
  gap: 1vw;

  .Auth-Error-Button {
    width: 70%;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: #EE5253;
    border: 1px solid #EE5253;
    color: #fff;
    font-size: 1rem;
    font-weight: 500;
    flex: 4 1 auto;

    &:hover {
      cursor: pointer;
      color: #EE5253;
      background-color: #fff;
      border: 1px solid #EE5253;
    }
  }
}

.Auth-Sign-In-Button-Container {

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  width: 75%;
  flex: 1 1 auto;

  // padding-top: 10px;
  gap: 1vw;

  .Auth-Sign-In-Button {
    width: 70%;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: #19AC84;
    border: 1px solid #19AC84;
    color: #fff;
    font-size: 1rem;
    font-weight: 500;
    margin-top: 10px;
    flex: 4 1 auto;

    &:hover {
      cursor: pointer;
      color: #19AC84;
      background-color: #fff;
      border: 1px solid #19AC84;
    }
  }

  .Auth-Sign-In-Later-Button {

    width: 30%;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: #fff;
    border: 1px solid #54A0FC;
    color: #54A0FC;
    font-size: 1rem;
    font-weight: 500;
    margin-top: 10px;
    flex: 1 1 auto;

    &:hover {
      cursor: pointer;
      color: #fff;
      background-color: #54A0FC;
      border: 1px solid #54A0FC;
    }

  }

}

.Auth-Sign-In-Input-Container {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;
  flex: 1 1 auto;

  >input {
    width: 70%;
    height: 35px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 0 10px;
    margin: 5px 0;
  }

  .Auth-Sign-In-Options-Container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;

    width: 75%;
    flex: 1 1 auto;

    margin-top: 5px;

    >a {
      color: #00000073;
      font-size: 0.8rem;
      text-decoration: none;
      text-align: right;
      white-space: nowrap;
    }

    >a:hover {
      cursor: pointer;
      text-decoration: underline;
    }

    .Auth-Sign-In-Remember-Me-Container {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;

      width: 100%;
      flex: 1 1 auto;

      >input {
        width: 13px;
        height: 13px;
        margin: 0 5px 0 0;

        &:hover {
          cursor: pointer;
        }
      }

      >label {
        font-size: 0.8rem;
      }
    }

  }

  >p {
    font-size: 0.8rem;
    text-align: center;
    position: relative;
    top: 5px;
    margin-top: 5px;

    >a {
      color: #00000073;
      text-decoration: none;
      text-align: right;
      white-space: nowrap;

      &:hover {
        cursor: pointer;
        text-decoration: underline;
      }
    }
  }

}

.Auth-Sign-In-Container {

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  // padding-top: 1.25rem;
  padding-top: 10px;
}

.Auth-Info-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  width: 100%;
  overflow-y: scroll;

  >h2 {
    font-weight: 400;
    color: #576574;
    padding: 0;
    font-size: 0.95rem;
    margin: 0rem 0rem 0.55rem;
  }

  >p {
    margin: 0;
    padding: 0;
    font-weight: 350;
    font-size: 0.8rem;
    color: #576574;
    line-height: 1.35rem;
    margin-bottom: 0.75rem;
  }
}