body {
  display: flex;
  flex: 1 1 auto;

  width: 545px;
  min-height: 340px;
  max-height: 650px;

  margin: 0;
  padding: 0;

  scrollbar-width: none;

  font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
  background-color: rgb(249, 249, 249);
}

#root {
  width: 100%;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}

// html, body { height: 100%; }
// body { margin: 0; font-family: Roboto, "Helvetica Neue", sans-serif; }

*:focus {
  outline: none !important;
}

h1 {
  margin: 0;
  padding: 0;

  font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
}

p {
  margin: 0;
  padding: 0;

  font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Toastify__toast {
  border-radius: 5px;
}

.Toastify__toast-body {
  font-size: 0.9rem;
}

.Popup-Container {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.55);
  -webkit-transform: translate3d(0,0,0);
  z-index: 10;
}

.Fade-In {
  animation: Fading 0.2s ease-in;
}

@keyframes Fading {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.Lds-Ring {
  display: flex;
  width: 80px;
  height: 80px;
}

.Lds-Ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #54a0fc;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #ffffff transparent transparent transparent;
}

.Lds-Ring div:nth-child(1) {
  animation-delay: -0.45s;
}

.Lds-Ring div:nth-child(2) {
  animation-delay: -0.3s;
}

.Lds-Ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.Popup-Back-Button {
  position: absolute;
  background: none;
  border: none;
  outline: none;
  width: 30px;
  height: 30px;
  left: 65px;
  top: 25px;

  >img {
    width: 100%;
    height: 100%;
  }

  &:hover {
    cursor: pointer;
  }
}


a:focus {
  outline: none;
  text-decoration: none;
  border-bottom: 0.1px dotted #000;
}