.Tos-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  width: 90%;
  max-height: 265px;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.06);
  background-color: #fff;
  border-radius: 5px;
  padding: 15px;
  z-index: 10;

  > h1 {
    padding: 0;
    font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #2e86de;
    font-size: 1.2rem;
    margin: 0 0 1rem;
  }
}

.Tos-Button-Container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  width: 100%;
  flex: 1 1 auto;
}

.Tos-Button {
  background: none;
  border: none;
  outline: none;
  border-radius: 5px;
  text-align: center;
  background-color: #19AC84;
  color: #fff;
  width: 50%;
  align-self: center;
  height: 150px;
  margin-top: 10px;

  &:hover {
    cursor: pointer;
  }
}

.Tos-Wrapper-Container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  flex: 1 1 auto;
  padding: 15px;
}

.Tos-Info-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  width: 100%;
  overflow-y: scroll;

  > h2 {
    font-weight: 400;
    color: #576574;
    padding: 0;
    font-size: 0.95rem;
    margin: 0rem 0rem 0.55rem;
  }

  > p {
    margin: 0;
    padding: 0;
    font-weight: 350;
    font-size: 0.8rem;
    color: #576574;
    line-height: 1.35rem;
    margin-bottom: 0.75rem;
  }
}
