.Settings-Container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 65%;
  height: 210px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.06);
  background-color: #fff;
  border-radius: 10px;
  padding: 35px;
  padding-top: 32px;
  margin-top: 22px;
  align-items: center;

  >h1 {
    padding: 0;
    font-family: Metropolis, "Avenir Next", "Helvetica Neue", Arial, sans-serif;
    font-weight: 300;
    font-style: normal;
    margin: 0 0 1px;
    color: #576574;
    font-size: 1.2rem;
    margin-bottom: 10px;
  }

}

.Renew-Subscription-Container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 2px;

  .Manage-Subscription-Button {
    background-color: transparent;
    border: none;
    outline: none;
    font-size: 0.60rem;
    font-weight: 600;
    color: #576574;
    padding-left: 0;
    padding-right: 0;

    &:hover {
      cursor: pointer;
      color: #FF8B1C;
    }
  }
}

.Settings-Canvas {
  position: absolute;
  z-index: -1;
  width: 90%;
  height: 100%;
}

.button-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  margin-top: 17px;

  >button {
    background-color: transparent;
    border: none;
    outline: none;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-size: 0.80rem !important;
    color: #576574 !important;
    padding-left: 0;

    &:hover {
      cursor: pointer;
    }
  }

  .button-icon {
    width: 21px !important;
    margin-right: 12px !important;
  }
}

.Logout-Container {

  display: flex;
  flex-direction: column;
  align-items: center;
  // Safari 15px looks better.
  gap: 15px;
  // gap: 25px;
  color: #576574;

  text-align: center;
  margin-top: 5vh;

  >h1 {
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 0;
  }

  .User-Data-Container {
    display: flex;
    flex-direction: column;
    gap: 2vh;
    color: #576574;

    >h1 {
      font-size: 0.90rem;
    }
  }

  .Logout-Button {
    width: 70%;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: #EE5253;
    border: 1px solid #EE5253;
    color: #fff;
    font-size: 1rem;
    font-weight: 500;
    margin-top: 10px;
    flex: 4 1 auto;

    &:hover {
      cursor: pointer;
      color: #EE5253;
      background-color: #fff;
      border: 1px solid #EE5253;
    }
  }


}

.Free-Trial-Container {
  display: flex;
  flex-direction: column;
  gap: 5vh;
  color: #576574;

  .Free-Trial-Body {

    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 4vw;

    >img {
      width: 85px;
      height: auto;
    }

    .Free-Trial-Body-Content {
      display: flex;
      flex-direction: column;
      gap: 2vh;

      >h1 {
        font-size: 1.5rem;
        font-weight: 500;
        margin-bottom: 0;
      }

      >p {
        font-size: 0.9rem;
        font-weight: 300;
        margin-top: 0;
        line-height: 1.2rem;
      }
    }
  }

  .Free-Trial-Button-Container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 2vw;

    >span {
      font-size: 0.9rem;
      font-weight: 500;
      position: relative;
      top: 5px;
    }

    .Free-Trial-Button {
      width: 70%;
      height: 40px;
      border: none;
      border-radius: 5px;
      background-color: #54A0FC;
      border: 1px solid #54A0FC;
      color: #fff;
      font-size: 1rem;
      font-weight: 500;
      margin-top: 10px;
      flex: 4 1 auto;

      &:hover {
        cursor: pointer;
        color: #54A0FC;
        background-color: #fff;
        border: 1px solid #54A0FC;
      }
    }

    .Subscribe-Button {

      width: 30%;
      height: 40px;
      border: none;
      border-radius: 5px;
      background-color: #fff;
      border: 1px solid #FF8B1C;
      color: #FF8B1C;
      font-size: 1rem;
      font-weight: 500;
      margin-top: 10px;
      flex: 1 1 auto;

      &:hover {
        cursor: pointer;
        color: #fff;
        background-color: #FF8B1C;
        border: 1px solid #FF8B1C;
      }
    }
  }

  >section {
    gap: 1vh;
    display: flex;
    flex-direction: column;

    >h1 {
      font-weight: 400;
    }

  }

  >section:nth-child(1) {
    gap: 1vh;
    display: flex;
    flex-direction: column;

    >h1 {
      font-weight: 500;
      text-align: center;
    }

  }
}



