.toggle-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;


  .item {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 100%;

    // margin-bottom: 15px;
    // On Safari MacOS, margin-bottom of 10px looks better.
