.Quizzard-Gold-Container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 1vw;

  >button {
    border-radius: 3px;
    padding-top: 2px;
    padding-bottom: 2px;
  }

  .Quizzard-Gold-Expiration-Label {
    font-weight: 500;
    opacity: 0.5;
    font-size: 0.75rem;
  }

  .Subscribe-Button {
    background-color: transparent;
    border: 1px solid #FF8B1C;
    outline: none;
    font-weight: 500;
    color: #FF8B1C;

    &:hover {
      cursor: pointer;
      font-weight: 500;
      color: white;
      background-color: #FF8B1C;
    }
  }

  .Trial-Button {
    background-color: transparent;
    border: 1px solid #5A8FFA;
    outline: none;
    font-weight: 400;
    color: #5A8FFA;

    &:disabled {
      cursor: not-allowed;
      opacity: 0.5;
    }

    &:hover:enabled {
      cursor: pointer;
      font-weight: 500;
      color: white;
      background-color: #5A8FFA;
    }
  }

}

.popup-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.55);
  -webkit-transform: translate3d(0, 0, 0);
  z-index: 10;
}

.Settings {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;

  width: 100%;
}

.switch {
  position: relative;
  display: inline-block;
  width: 35px;
  height: 20px;
  outline: none;
}

.switch input {
  position: absolute;
  top: -99999px;
  left: -99999px;
}

.Slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 34px;
  transition: transform 300ms linear;
  will-change: transform, background-color, box-shadow;
}

.Slider::before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 4px;
  bottom: 3px;
  background-color: white;
  border-radius: 50%;
  transition: transform 300ms linear;
  will-change: transform, background-color, box-shadow;
}

.Slide-In {
  -webkit-transform: translateX(14px);
  transform: translateX(14px);
  transition: transform 300ms linear;

  background-color: #19AC84;
  box-shadow: 0 0 1px #19AC84;

  will-change: transform, background-color, box-shadow;
}

input:checked+.Slider {
  background-color: #19AC84;
  will-change: transform, background-color, box-shadow;
}

input:focus+.Slider {
  box-shadow: 0 0 1px #19AC84;
  will-change: transform, background-color, box-shadow;
}

input:checked+.Slider::before {
  -webkit-transform: translateX(14px);
  transform: translateX(14px);
  will-change: transform, background-color, box-shadow;
}

.Fade-In {
  animation: Fading 0.2s ease-in;
}

@keyframes Fading {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.Settings-Close-Button {
  position: absolute;
  background: none;
  border: none;
  outline: none;
  width: 30px;
  height: 30px;
  right: 65px;
  top: 25px;

  >img {
    width: 100%;
    height: 100%;
  }

  &:hover {
    cursor: pointer;
  }
}

.input-hover:hover {
  opacity: 1;
  cursor: pointer;
}